html, body, #root {
  overflow-x: hidden;
}

.brian-navbar {
  width:100%;
  padding:1rem 0 !important;
  background-size:100%;
  background-repeat:no-repeat;
  background-color:#fff;
  background-image:url('../images/main-background.jpg') !important;
  background-size: 100%;
  background-repeat: no-repeat;
  box-shadow: 0 -7px 10px -10px rgba(0, 0, 0, .95);
  border-bottom:1px solid rgba(0,0,0,.125);
}

.brian-logo-text-top, .brian-logo-text-top-mobile {
  margin-bottom:.1rem;
  font-family: 'Roboto', sans-serif;
  font-weight:400;
  text-transform: capitalize;
}

.brian-logo-text-top {
  font-size:1.25rem;
  text-align:center;
  white-space:nowrap;
}

.brian-logo-text-top-mobile {
  font-size:.95rem;
}

.brian-logo-text-bottom {
  font-family: 'Roboto', sans-serif;
  font-size:.6rem;
  text-align:right;
  font-weight:500;
  text-transform: uppercase;
  margin-top:-.25rem;
  margin-bottom:0;
}

.brian-logo-text-bottom-mobile {
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  font-size:8px;
  font-weight:500;
  text-align:right;
  margin-top:-.25rem;
  margin-bottom:0;
}

.brian-logo {
  width:3.25rem;
  margin-left:.5rem;
  margin-right:2rem;
}

.brian-logo-mobile {
  width:2.5rem;
  margin-left:.25rem;
  margin-right:.5rem;
}

.brian-logo-text-container {
  text-align:center;
  margin-left: 3.5rem;
  white-space:nowrap;
}

.brian-logo-text-container-mobile {
  margin-left:1.5rem;
  text-align:left;
  white-space:nowrap;
}

.brian-nav-link, .brian-nav-link-mobile {
  position:relative;
  padding: .25rem .5rem !important;
  color:#041b3a;
  font-size:.8rem !important;
  text-transform: capitalize;
  color:rgb(74,74,74);
}

.brian-nav-link {
  margin:0 1rem;
  font-size:.85rem !important;
}

.brian-nav-link-mobile {
  font-size:.75rem !important;
  margin-left:.25rem !important;
  margin-right:.25rem !important;
}

.brian-dropdown-link > a {
    color:rgb(74,74,74);
}

.brian-nav-link:hover, .vo-dropdown-link > a:hover {
    color:rgb(74,74,74);
  }

.brian-nav-link:before {
    content: "";
    position: absolute;
    width: 100%;
    height: .2rem;
    bottom: -.2rem;
    left: 0;
    background-color:rgb(74,74,74);
    visibility: hidden;
    transform: scaleX(0);
    transition: all 0.3s ease-in-out 0s;
    
}

.brian-nav-link:hover:before {
    visibility: visible;
    transform: scaleX(1);
  }

  .nav-email-button {
    position: absolute;
    right: 2rem;
    padding:.25rem 2rem !important;
    background-color: #0947a5 !important;
    border-color: #0947a5 !important;
  }

  .nav-email-button:hover {
    background-color: #003597cb !important;
    border-color: #003597cb !important;
  }

  #login-button {
    background: #1ec31e;
    border:1px solid #19a119;
  }

  #login-button:hover {
    background:#19a119;
  }
  
  @keyframes rotation {
    from {
        -webkit-transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(359deg);
    }
  }
  